<template>
<div>
    <v-app-bar color="#003263" dark src="../assets/images/vbanner5.jpg">
        <template v-slot:img="{ props }">
            <v-img v-bind="props" gradient="to top right, rgb(1,48,98,.9), rgb(22,61,99,.8)"></v-img>
        </template>

        <div>
            <img src="../assets/images/ghub_white_.png" style="margin-top:50px;" width="150" class="bounceIn">
        </div>

        <v-spacer></v-spacer>
        <v-toolbar-title>Accounts Management</v-toolbar-title>

        <template v-slot:extension>

            <v-tabs right v-model="tab">

                <v-tab v-for="item in items" :key="item">
                    {{ item }}
                </v-tab>
            </v-tabs>
        </template>

    </v-app-bar>
    <div style="background-color:#EBEFF3;height:88vh;overflow-y:auto;display: flex;justify-content: center;">
        <br>

        <v-card width="1000" class="ma-5" height="750" v-if="tab == 0">
            <v-toolbar dark color="red" flat>
                <v-toolbar-title>Journal Expence List</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="blue" dark class="mr-5" @click="dialogNewProduct = true">
                    <v-icon left>
                        add
                    </v-icon>
                    Add Item
                </v-btn>
                <v-btn color="orange" dark class="mr-5" @click="dialogNewProduct2 = true">
                    <v-icon left>
                        add
                    </v-icon>
                    Add Expense Item
                </v-btn>
                <download-csv :data="products" :fields="['logisticsId','docDate','supplierName','currencyISO','weight','cost','description']">
                    <v-btn color="green" dark class="mr-5">
                        <v-icon left>
                            add
                        </v-icon>
                        Export
                    </v-btn>
                </download-csv>

            </v-toolbar>

            <v-data-table :headers="headersProducts" :items="products" :search="search" sort-by="id" sort-desc>
                <template v-slot:top>
                    <v-toolbar flat color="white">

                        <v-spacer></v-spacer>
                        <v-btn color="blue" dark class="mr-5" @click="journalsToday">
                            <v-icon left>
                                search
                            </v-icon>
                            Today
                        </v-btn>
                        <v-btn color="blue" dark class="mr-5" @click="dialogSpecific = true">
                            <v-icon left>
                                search
                            </v-icon>
                            Specfic Date
                        </v-btn>
                    </v-toolbar>
                </template>

                <template v-slot:item.credit="{ item }">

                    <v-chip color="green" dark v-if="item.currency == '1'">
                        USD {{ parseFloat(item.credit).toFixed(2) }}
                    </v-chip>

                    <v-chip color="orange" dark v-if="item.currency == '2'">
                        ZWL {{ parseFloat(item.credit).toFixed(2) }}
                    </v-chip>

                </template>
                <template v-slot:item.actions="{ item }">

                    <v-tooltip bottom v-if="item.status != 0">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="red" class="mr-2" @click="deleteItem(item)">
                                delete
                            </v-icon>
                        </template>
                        <span>Delete</span>
                    </v-tooltip>

                </template>

                <template v-slot:no-data>
                    <h3>No Records Found</h3>
                </template>

            </v-data-table>

        </v-card>

    </div>

    <v-dialog v-model="dialogNewProduct" max-width="750px">

        <v-card>
            <v-card-title class="headline blue lighten-2 white--text" primary-title>
                <span class="headline">Create New Expense Journal</span>
                <v-spacer></v-spacer>
                <v-btn-toggle v-model="deliverNote.docCurrency" color="blue" background-color="white">
                    <v-btn value="1">
                        USD
                    </v-btn>

                    <v-btn value="2">
                        ZWL
                    </v-btn>
                </v-btn-toggle>

            </v-card-title>

            <v-card-text>
                <v-container>
                    <v-row>
                        <v-col cols="12" sm="12" md="6">
                            <div style="display: flex;justify-content: start;">
                                <span>Journal Date</span>
                            </div>
                            <v-text-field v-model="deliverNote.docDate" solo outlined type="date"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12" md="6">
                            <div style="display: flex;justify-content: start;">
                                <span>Expense Name</span>
                            </div>
                            <v-autocomplete item-text="expenseName" item-value="expenseName" v-model="deliverNote.expenseName" :items="customers" solo outlined></v-autocomplete>

                        </v-col>

                        <v-col cols="12" sm="12" md="12">
                            <v-text-field v-model="deliverNote.cost" label="Expense Amount" solo outlined></v-text-field>
                        </v-col>

                    </v-row>
                </v-container>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" dark @click="dialogNewProduct = false">Cancel</v-btn>
                <v-btn color="blue darken-1" dark @click="saveProduct">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogNewProduct2" max-width="600px">

        <v-card>
            <v-card-title class="headline red lighten-2 white--text" primary-title>
                <span class="headline">New Expense Item</span>
                <v-spacer></v-spacer>

            </v-card-title>

            <v-container>
                <v-row>

                    <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="expenseName" label="Expense Name" solo outlined></v-text-field>
                    </v-col>

                </v-row>
            </v-container>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" dark @click="dialogNewProduct2 = false">Cancel</v-btn>
                <v-btn color="blue darken-1" dark @click="saveProductUpdate">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogSpecific" max-width="600px">

        <v-card>
            <v-card-title class="headline red lighten-2 white--text" primary-title>
                <span class="headline">Search Journal by Date</span>
                <v-spacer></v-spacer>

            </v-card-title>

            <v-container>
                <v-row>
                    <v-col cols="12" sm="12" md="12">
                        <v-text-field type="date" v-model="startDate" solo outlined></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                        <v-text-field type="date" v-model="endDate" solo outlined></v-text-field>
                    </v-col>
                </v-row>
            </v-container>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" dark @click="dialogSpecific = false">Cancel</v-btn>
                <v-btn color="blue darken-1" dark @click="journalsSpecific">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="#003263" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>

</div>
</template>

<script>
export default {
    data() {
        return {
            startDate: '',
            endDate: '',
            dialogSpecific: false,
            expenseName: '',
            dialogloader: false,
            dialogNewProduct: false,
            dialogNewProduct2: false,
            dialogDelete: false,
            currency: 'USD',
            nodeApi: '',
            selectedCustomer: '',
            tab: 0,
            items: ['Expenses'],
            search: '',
            pagination: {
                itemsPerPage: 15,

            },
            productDetails: {
                productCurrency: '1',
                productName: '',
                productSize: '',
                productPrice: ''
            },
            deliverNote: {
                docDate: '',
                expenseName: '',
                cost: '',
                docCurrency: '1',

            },
            salesOrder: {

                docDate: '',
                docCurrency: '',
                description: '',
                itemData: [],
                docTotal: 0
            },
            customers: [],
            snack: false,
            snackColor: '',
            snackText: '',
            max25chars: v => v.length <= 25 || 'Input too long!',

            headersProducts: [{

                    text: 'Expense ID',
                    align: 'start',
                    sortable: false,
                    value: 'id',
                },
                {
                    text: 'Journal Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Expense Name',
                    align: 'start',
                    sortable: false,
                    value: 'description',
                },

                {
                    text: 'Amount',
                    align: 'start',
                    sortable: false,
                    value: 'credit',
                },
                {
                    text: 'Actions',
                    value: 'actions'
                },

            ],
            headers: [{
                    text: 'Product ID',
                    align: 'start',
                    sortable: false,
                    value: 'productId',
                },
                {
                    text: 'Product Name',
                    align: 'start',
                    sortable: false,
                    value: 'productName',
                },
                {
                    text: 'Quantity',
                    value: 'quantity'
                },
                {
                    text: 'Size',
                    value: 'productSize'
                },
                {
                    text: 'Unit Price',
                    value: 'price'
                },
                {
                    text: 'TAX',
                    value: 'tax'
                },
                {
                    text: 'Line Total (Inc Tax)',
                    value: 'linetotal'
                },
                {
                    text: 'Actions',
                    value: 'actions'
                },
            ],
            headersSales: [{

                    text: 'SalesOrder ID',
                    align: 'start',
                    sortable: false,
                    value: 'salesOrderId',
                },
                {
                    text: 'Sales Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Customer Name',
                    align: 'start',
                    sortable: false,
                    value: 'customerName',
                },
                {
                    text: 'Sales Total',
                    align: 'start',
                    sortable: false,
                    value: 'docTotal',
                },

            ],
            lineItems: [],
            editedIndex: -1,
            products: [],
            productName: '',
            productCurrency: '',
            productSize: '',
            productPrice: '',
            productId: '',
            itemProducts: [],
            docCurrency: '1',
            salesOrdersData: [],
            expenseId: ''

        }
    },
    mounted() {
        this.initialize()
        this.getProducts()
        this.getAllOrders()
        const today = new Date();
        const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
        this.salesOrder.docDate = date
    },
    watch: {
        docCurrency(val) {
            let data = val
            this.salesOrder.docCurrency = data
            if (val == '1') {
                this.currency = 'USD'
                this.deliverNote.docCurrency = data
            }
            if (val == '2') {
                this.currency = 'ZWL'
                this.deliverNote.docCurrency = data
            }
            this.clearForm()
            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.deliverNote.docDate = date
            // console.log(this.salesOrder.docCurrency)
            this.getProducts()
        },
        dialogNewProduct(val) {
            if (val) {
                this.deliverNote = {
                    docDate: '',
                    expenseName: '',
                    cost: '',
                    docCurrency: '1',
                }
                const today = new Date();
                const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
                this.deliverNote.docDate = date
            }
        },
        dialogNewProduct2(val) {
            if (val) {
                this.productDetails = {
                    productCurrency: '1',
                    productName: '',
                    productSize: '',
                    productPrice: ''
                }
            }
        },

    },
    methods: {
        editItem(data) {

            this.productDetails = Object.assign({}, data)
            this.productName = data.productName
            this.productCurrency = data.productCurrency.toString()
            this.productSize = data.productSize
            this.productPrice = data.productPrice
            this.productId = data.productId
            console.log(this.productCurrency)
            this.dialogNewProduct2 = true
        },
        saveProductUpdate() {
            if (!this.expenseName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Expense Item Failed',
                    text: 'Please enter a valid Expense Item.'
                })
            } else {

                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addExpense', {
                    expenseName: this.expenseName
                }).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.initialize()
                        this.dialogNewProduct2 = false
                        this.dialogloader = false
                        this.expenseName = ''
                        this.$swal.fire({
                            type: 'success',
                            title: 'Expense Item Added',
                            text: 'You have successfully added a Expense Item'
                        })
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Expense Item Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Expense Item Failed Failed',
                        text: err
                    })
                })

            }
        },
        saveProduct() {

            if (!this.deliverNote.docDate) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid document Date.'
                })
            } else if (!this.deliverNote.expenseName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid supplier.'
                })
            } else if (!this.deliverNote.cost) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid cost amount'
                })
            } else if (isNaN(this.deliverNote.cost)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid Product price (it has to be a number)'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.deliverNote.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addJournalExpense', this.deliverNote).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.getProducts()
                        this.dialogNewProduct = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Journal Added',
                            text: 'You have successfully added a new Journal note'
                        })
                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Journal Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Delivery Failed',
                        text: err
                    })
                })

            }
        },
        addItem() {
            this.lineItems.push({
                productId: '1',
                productName: 'Click to Edit',
                quantity: 1,
                price: 0,
                tax: 14.5,
                linetotal: 0,
            })
        },
        clearForm() {
            this.salesOrder = {
                customerId: '',
                docDate: '',
                docCurrency: '1',
                description: '',
                itemData: [],
                docTotal: 0
            }
            this.lineItems = []

            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.salesOrder.docDate = date
        },
        closeDelete() {
            this.dialogDelete = false
            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            this.editedIndex = this.products.indexOf(item)
            this.expenseId = item.id
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.products.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeExpenseIdList', {
                expenseId: this.expenseId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Journal Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Journal Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })
        },
        getAllOrders() {
            this.axios.get(this.nodeApi + '/getAllSalesOrders').then((response) => {
                this.salesOrdersData = response.data

            }).catch(err => {
                console.log(err)
            })
        },
        getProducts() {
            this.axios.get(this.nodeApi + '/getJournalExpenses').then((response) => {
                this.products = response.data

            }).catch(err => {
                console.log(err)
            })

        },
        initialize() {
            this.axios.get(this.nodeApi + '/getAllExpenses').then((response) => {

                this.customers = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        journalsToday() {
            this.axios.get(this.nodeApi + '/getJournalExpensesToday').then((response) => {

                this.products = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        journalsSpecific() {
            this.axios.post(this.nodeApi + '/getJournalExpensesSpecific', {
                startDate: this.startDate,
                endDate: this.endDate
            }).then((response) => {

                this.products = response.data
                this.dialogSpecific = false

            }).catch(err => {
                console.log(err)
            })

        },
        save(data) {
            console.log(data)

            const result = this.products.find(({
                productName
            }) => productName === data.productName);
            let index = this.lineItems.indexOf(data)
            if (data.productName == result.productName) {
                this.lineItems[index].productId = result.productId
                this.lineItems[index].price = result.productPrice
                this.lineItems[index].productSize = result.productSize

                this.lineItems[index].linetotal = (result.productPrice * data.quantity) + (((result.productPrice * data.quantity) * data.tax) / 100)
                this.lineItems.forEach(element => {
                    this.salesOrder.docTotal += element.linetotal
                })

            } else {
                console.log('Data Mismatch')
            }

            this.snack = true
            this.snackColor = 'success'
            this.snackText = 'Data saved'
        },
        cancel() {
            this.snack = true
            this.snackColor = 'error'
            this.snackText = 'Canceled'
        },
        open() {
            this.snack = true
            this.snackColor = 'info'
            this.snackText = 'Dialog opened'
        },
        close() {
            console.log('Dialog closed')
        },

        saveSalesOrder() {
            this.dialogloader = true
            this.salesOrder.itemData = JSON.stringify(this.lineItems)
            this.salesOrder.docCurrency = this.docCurrency
            this.salesOrder.createdBy = this.$session.get('user').userId
            // console.log(this.salesOrder)

            this.axios.post(this.nodeApi + '/addSalesOrder', this.salesOrder).then((response) => {
                // console.log(response.data)
                if (response.data.length > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Sales Order Added',
                        text: 'You have successfully added a new sales order'
                    })
                    this.clearForm()
                    this.getAllOrders()
                    this.dialogloader = false
                } else {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Sales Order Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {
                this.dialogloader = false
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Sales Order Failed',
                    text: err
                })
            })

        }
    },
}
</script>

<style>

</style>
